import './css/main.css'
import Vue from 'vue'
import store from './store'
import Book from './components/book.vue'
import restore from './tools/restore'
// import font from './entrance/font'
import cache from './tools/cache'
import {
    RESTORE
} from './store/action-types'
export default function(selector) {
    cache.AUTHENTIC_KEY = document.getElementById('auth_key').value
    let $rootEl = cache.rootEl = document.querySelector(selector)

    restore({
        left: 0,
        top: 0,
        right: $rootEl.offsetWidth,
        bottom: $rootEl.offsetHeight
    }, function(data) {
        store.dispatch(RESTORE, data)
        new Vue({
            store,
            render: h => h(Book)
        }).$mount(selector)
    })

    // font(this, store)
}


